.add {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 3rem;
}

.header {
  display: flex;
  flex-direction: column;
  width: 60%;
  align-items: center;
  background: rgb(98, 22, 22);
  color: #fefefe;
  width: 100%;
}

form {
  display: flex;
  gap: 2vh;
  margin-bottom: 1rem;
}

form div {
  display: flex;
  flex-direction: column;
}

input {
  border-radius: 10px;
}

.table {
  width: 50%;
  border-radius: 20px;
  background: rgb(220, 224, 213);
  color: rgb(76, 85, 86);
}

.err {
  color: red;
}

.errBorder {
  border: 1px solid red;
}/*# sourceMappingURL=add.module.css.map */